import React, {Component} from 'react';
import classNames from './CheckOut.module.css';
import CS from '../../components/Order/CheckoutSummery/CheckoutSummery';
import Aux from '../../hoc/Auxiliary';
import {connect} from 'react-redux'

class CheckOut extends Component{

    cancelAction = () => {
        this.props.history.goBack();
    }
    continueAction = () => {
        this.props.history.push('/checkout/payment')
    }
    addAction = () => {
        this.props.history.push('/')
    }
    render() {
        return (
            <Aux>
                <div className={classNames.container}>
                    <h1>Checkout</h1>
                </div>
                <CS ingredients={this.props.ig} cancel={this.cancelAction} continue={this.continueAction} add={this.addAction}></CS>
            </Aux>
        );
    }
}

const mapStatetoProps = state => {
    return {
        ig: state.burger.ingredients,
    }
}

export default connect(mapStatetoProps, null)(CheckOut);