import * as actionTypes from '../actions/actions';
import { updateObj } from '../utility'

const initialState = {
    ingredients: {
        salad: 0,
        bacon: 0,
        meat: 0,
        cheese: 0
    },
    totalPrice : null,
};

const INGREDIENTS_PRICE = {
    salad:1.5,
    bacon:1.2,
    meat:2,
    cheese:1.7
}

const Reducer = (state = initialState, action)=>{
    switch (action.type) {
        case actionTypes.ADD_IG:
            return updateObj(state, {
                ingredients: {
                    ...state.ingredients,
                    [action.igName] : state.ingredients[action.igName] + 1
                },
                totalPrice: state.totalPrice + INGREDIENTS_PRICE[action.igName]
            })
        case actionTypes.REMOVE_IG:
            return updateObj(state, {
                ingredients: {
                    ...state.ingredients,
                    [action.igName] : state.ingredients[action.igName] - 1
                },
                totalPrice: state.totalPrice + INGREDIENTS_PRICE[action.igName]
            })
        case actionTypes.SET_IG:
            return updateObj(state, {
                ingredients: action.ingredients
            })
        case actionTypes.SET_PRICE:
            return updateObj(state, {
                totalPrice: action.totalPrice
            })
        default:
            return state
    };
};

export default Reducer;