import * as actionTypes from './actions';
import axios from '../../axios-order';

export const addIG = (name) => {
    return{
        type: actionTypes.ADD_IG,
        igName: name
    }
}
export const removeIG = (name) => {
    return{
        type: actionTypes.REMOVE_IG,
        igName: name
    }
}
export const setIg = (ig) => {
    return{
        type: actionTypes.SET_IG,
        ingredients: ig
    }
}
// export const errorIg = () => {
//     return{
//         type: actionTypes.ERROR_IG
//     }
// }
export const initIg = () => {
    return dispatch => {
        axios.get('/ingredients.json').then(data=>{
            dispatch(setIg(data.data))
        }).catch(error=>{
            console.log(error);
            // dispatch(errorIg())
        })
    }
}
export const setPrice = (price) => {
    return{
        type: actionTypes.SET_PRICE,
        totalPrice: price 
    }
}
export const initPrice = () => {
    return dispatch => {
        axios.get('/price.json').then(data=>{
            dispatch(setPrice(data.data));
        }).catch(error=>{
            console.log(error)
        })
    }
}