import React, { Component } from 'react';
import Aux from '../Auxiliary';
import Modal from '../../components/UI/Modal/Modal';

const errHandle = (WrappedElements, axios) => {
    


    return class extends Component {
        state = {
            error : null
        }

        componentDidMount () {
            this.req = axios.interceptors.request.use(req=>{
                this.setState({error:null})
                return req;
            })
            this.res = axios.interceptors.response.use(res => res, error=>{
                this.setState({error: error})
            })
        }

        componentWillUnmount () {
            axios.interceptors.request.eject(this.req)
            axios.interceptors.response.eject(this.res)
        }

        errorClear = () => {
            this.setState({error:null})
        }

        render () {
            return (
                <Aux>
                    <Modal show={this.state.error} modalClose={this.errorClear}>
                        {this.state.error ? this.state.error.message : null}
                    </Modal>
                    <WrappedElements {...this.props} />
                </Aux>
            )
        }
    }
}

export default errHandle;