import React, { Component } from 'react';
import {connect} from 'react-redux';
import Order from '../../components/Order/Order';
import Axios from '../../axios-order';
import Spinner from '../../components/UI/Spinner/Spinner';
import Modal from '../../components/UI/Modal/Modal';
import BTN from '../../components/UI/Button/Button';

class Orders extends Component {
    state = {
        orders: null,
        loading: true,
        modal: false,
        error: null
    }

    componentDidMount(){
        Axios.get(`/orders.json?auth=${this.props.token}&orderBy="userId"&equalTo="${this.props.uId}"`).then(data=>{
            const orders = [];
            for( let key in data.data){
                orders.push({
                    ...data.data[key],
                    id: key
                })
            }
            this.setState({
                orders:orders,
                loading: false
            })
        }).catch(err=>{
            this.setState({
                loading: false,
                modal: true,
                error: err.response.data.error
            })
        })
    }

    modalClose = () => {
        this.setState({modal: false})
    }

    loginPage = () => {
        this.props.history.push('/auth');
    }

    render() {
        return (
            <>
            {this.state.loading ? <Spinner /> : this.state.orders ?  this.state.orders.map(order=>{
                return <Order key={order.id} ig={order.ingredients} price={Number(order.price).toFixed(2)} />
            }) : <Modal show={this.state.modal} modalClose={this.modalClose} > <h1 style={{textAlign:'center',color:'red'}}>{this.state.error}</h1>  <BTN btnType='Success' btnClick={this.loginPage} > Login </BTN> </Modal> }
            </>
        );
    }
}

const mapStatetoProps = state => {
    return{
        token: state.auth.token,
        uId: state.auth.userId
    }
}

export default connect(mapStatetoProps, null)(Orders);