import React from 'react';
import classNames from './BuildControl.module.css';

const BuildControl = (props) => {
    return (
        <div className={classNames.BuildControl}>
            <div className={classNames.Label} >{props.label}</div>
            <button className={classNames.Less} onClick={props.removing} disabled={props.disable} > Less </button>
            <button className={classNames.More} onClick={props.addition} > More </button>
        </div>
    );
};

export default BuildControl;