import * as actionTypes from './actions';
import Axios from 'axios';

export const authStart = () => {
    return {
        type: actionTypes.AUTH_START
    };
};

export const authSuccess = (resToken, resUserId) => {
    return {
        type: actionTypes.AUTH_SUCCESS,
        token: resToken,
        userId: resUserId
    };
};

export const authFail = (error) => {
    return {
        type: actionTypes.AUTH_FAIL,
        error: error
    };
};

export const logout = () => {
    localStorage.removeItem('token')
    localStorage.removeItem('expireIn')
    localStorage.removeItem('userId')
    localStorage.removeItem('access_token')
    return {
        type: actionTypes.AUTH_LOGOUT
    }
}

export const authLogout = (expTime) => {
    return dispatch => {
        setTimeout(() => {
            dispatch(logout())
        }, expTime * 1000);
    }
}

export const auth = (email, password, isSignUp) => {
    return dispatch => {
        dispatch(authStart());
        const userData = {
            email: email,
            password: password,
            returnSecureToken: true
        }

        let URL = 'https://identitytoolkit.googleapis.com/v1/accounts:signUp?key=';

        if(!isSignUp){
            URL = 'https://identitytoolkit.googleapis.com/v1/accounts:signInWithPassword?key=';
        }

        Axios.post(`${URL}AIzaSyBZAIKaHhpDiqM4DoQE8eHghEmHwGgOsSk`, userData)
        .then((res)=>{
            alert('You are now logged in');
            const expTime = new Date(new Date().getTime() + res.data.expiresIn * 1000);
            localStorage.setItem('token', res.data.idToken)
            localStorage.setItem('expireIn', expTime)
            localStorage.setItem('userId', res.data.localId)
            dispatch(authSuccess(res.data.idToken, res.data.localId));
            dispatch(authLogout(res.data.expiresIn))
        }).catch(err=>{
            dispatch(authFail(err.response.data.error.message));
        })
    };
};

export const authLocal = () => {
    return dispatch => {
        const token = localStorage.getItem('token');
        if(!token){
            dispatch(logout())
        }else{
            const expTime = new Date(localStorage.getItem('expireIn'))
            if(expTime <= new Date()){
                dispatch(logout());
            }else{
                const userId = localStorage.getItem('userId');    
                dispatch(authSuccess(token, userId));
                dispatch(authLogout(expTime.getTime() - new Date().getTime()/1000))
            }
        }

    }
}