import React from 'react';
import classNames from './NavItem.module.css';
import {NavLink} from 'react-router-dom';

const NavItem = (props) => {
    const home = props.link === '/';
    return (
        <li className={classNames.NavItem}> <NavLink exact={home} to={props.link} activeClassName={classNames.active}> {props.children} </NavLink> </li>
    );
};

export default NavItem;