import * as actionTypes from './actions';
import axios from '../../axios-order';

export const buyBurgerSucces = (id, orderData) => {
    return {
        type: actionTypes.BUY_BURGER_SUCCESS,
        orderId: id,
        orderData: orderData
    }
}

export const buyBurgerError = (error) => {
    return {
        type: actionTypes.BUY_BURGER_ERROR,
        error: error,
    }
}

export const loading = ()=>{
    return{
        type: actionTypes.BUY_BURGER
    }
}

export const buyBurgerDone = ()=>{
    return{
        type: actionTypes.BUY_BURGER_DONE
    }
}

export const buyNew = ()=>{
    return{
        type: actionTypes.BUY_BURGER_NEW
    }
}

export const buyBurger = (orderData, token) => {
    return dispatch => {
        dispatch(loading())
        setTimeout(() => {
            axios.post('/orders.json?auth=' + token, orderData).then(data=>{
                dispatch(buyBurgerSucces(data.data, orderData))
            }).then(()=>{
                dispatch(buyBurgerDone())
            }).catch(error=>{
                dispatch(buyBurgerError(error))
            })
        }, 1000);
    }
}

export const buyNewBurger = () => {
    return dispatch => {
        dispatch(buyNew())
    }
}