import * as actionTypes from '../actions/actions';

const initState = {
    orders: [],
    loading: null,
    redirect: false
}

const reducer = (state = initState, actions) => {
    switch (actions.type) {
        case actionTypes.BUY_BURGER_DONE:
            return{
                ...state,
                redirect: true
            }
        case actionTypes.BUY_BURGER_NEW:
            return{
                ...state,
                redirect: false
            }
        case actionTypes.BUY_BURGER:
            return{
                ...state,
                loading: true
            }
        case actionTypes.BUY_BURGER_SUCCESS:
            const newOrder = {
                ...actions.orderData,
                id: actions.orderId.name
            }
            return{
                ...state,
                loading: false,
                orders: state.orders.concat(newOrder),
            }
        case actionTypes.BUY_BURGER_ERROR:
            return{
                ...state,
                loading: false,
            }
    
        default:
            return state
    }
}

export default reducer;