import React from 'react';
import classNames from './SideDrawer.module.css';
import Logo from '../../Logo/Logo';
import Nav from '../NavItems/NavItems';
import Backdrop from '../../UI/Backdrop/Backdrop';
import Aux from "../../../hoc/Auxiliary";

const SideDrawer = (props) => {
    return (
        <Aux>
            <Backdrop show={props.show} clicked={props.menuClose} />
            <div className={`${classNames.SideDrawer} ${props.show ? classNames.Open: classNames.Close}`}>
                <div className={classNames.close} onClick={props.menuClose}><h2>X</h2></div>
                <Logo height='10%' />
                <nav>
                    <Nav  routeCtrlInner={props.routeCtrl} />
                </nav>
            </div>
        </Aux>
    );
};

export default SideDrawer;