import React, { Component } from 'react';
import classNames from './Payment.module.css';
import BTN from '../../UI/Button/Button'
import {connect} from 'react-redux';
import axios from '../../../axios-order';
import errorHandler from '../../../hoc/ErrorHandler/ErrorHandler';
import * as actions from '../../../store/actions/index';
import Modal from '../../UI/Modal/Modal';
import Aux from '../../../hoc/Auxiliary';

class Payment extends Component {
    state = {
        userData:{
            name: '',
            email: '',
            address: {
                street: '',
                zip: null,
                country: '',
            },
            card: null,
            date:{
                month: null,
                year: null,
            },
            cvv: null
        },
        cardValue: '',
        monthValue: '',
        yearValue: '',
        cvvValue: ''
    }
    order = (event) => {
        event.preventDefault();
        const order = {
            ingredients: this.props.ig,
            price: Number(this.props.price),
            userData: this.state.userData,
            userId: this.props.uid
        }
        if(Object.values(this.props.ig).reduce((p,c)=>p+c) > 0 ){
            this.props.buy(order, this.props.token);
        }else{
            alert('Please add ingredients to order!')
            this.props.history.push('/')
        }
    }

    inputHandler = (event, data) => {
        let userDataInput = {...this.state.userData}
        switch (data) {
            case 'name':
                userDataInput.name = event.target.value;
                this.setState({userData:userDataInput})
                break;
            case 'email':
                userDataInput.email = event.target.value;
                this.setState({userData:userDataInput})
                break;
            case 'street':
                userDataInput.address.street = event.target.value;
                this.setState({userData:userDataInput})
                break;
            case 'zip':
                userDataInput.address.zip = event.target.value;
                this.setState({userData:userDataInput})
                break;
            case 'country':
                userDataInput.address.country = event.target.value;
                this.setState({userData:userDataInput})
                break;
            case 'card':
                if(event.target.value.length <= 19){
                    event.target.value = event.target.value.replace(/[^\dA-Z]/g, '').replace(/(.{4})/g, '$1 ').trim();
                    userDataInput.card = event.target.value;
                    this.setState({
                        userData:userDataInput,
                        cardValue:event.target.value
                    })
                }
                break;
            case 'month':
                if(event.target.value.length === 2){
                    const monthRegex = /^(0[1-9]|1[0-2])$/;
                    let value = !monthRegex.test(event.target.value) ? event.target.value.replace(event.target.value, '') : event.target.value
                    userDataInput.date.month = value;
                    this.setState({
                        userData:userDataInput,
                        monthValue: value
                    })
                }else{
                    event.target.value = event.target.value.replace(/[^\dA-Z]/g, '').replace(/(.{4})/g, '$1 ').trim();
                    userDataInput.date.month = event.target.value;
                    this.setState({
                        userData:userDataInput,
                        monthValue: event.target.value
                    })
                }
                break;
            case 'year':
                if(event.target.value.length <= 4){
                    event.target.value = event.target.value.replace(/[^\dA-Z]/g, '').replace(/(.{4})/g, '$1 ').trim();
                    userDataInput.date.year = event.target.value;
                    this.setState({
                        userData:userDataInput,
                        yearValue: event.target.value
                    })
                }
                break;
            case 'cvv':
                if(event.target.value.length <= 3){
                    event.target.value = event.target.value.replace(/[^\dA-Z]/g, '').replace(/(.{4})/g, '$1 ').trim()
                    userDataInput.cvs = event.target.value;
                    this.setState({
                        userData:userDataInput,
                        cvvValue: event.target.value
                    })
                }
                break;
            default:
                this.setState({userData:userDataInput})
                break;
        }
    }
    modalClose = () => {
        new Promise((resolve)=>{
            this.props.newB();
            resolve()
        }).then(()=>{
            this.props.history.push('/')
        })
    }
    redirect = () => {
        this.props.newB();
        this.props.history.push('/')
    }
    loginPage = () => {
        this.props.history.push('/auth');
    }

    render() {
        return (
            <Aux>
                {!this.props.token? <Modal show={true}> <h1 style={{textAlign:'center',color:'red'}}>Please login/signup to continue</h1>  <BTN btnType='Success' btnClick={this.loginPage} > Login </BTN> </Modal> :null}
                <Modal  show={this.props.redirect} modalClose={this.modalClose} loader={this.state.loader} >
                    <h1 style={{textAlign: 'center'}}>Your Burger is getting ready!</h1>
                    <h2 style={{textAlign: 'center'}}>Please wait</h2>
                    <div className={classNames.modalBtn}>
                        <BTN  btnType='Success' btnClick={this.redirect}>Okay</BTN>
                    </div>
                </Modal>
                <div className={classNames.container}>
                    <div className={classNames.inner}>
                        <h1>Payment</h1>
                        <div className={classNames.formCon}>
                            <form>
                                <input onChange={ (event) => this.inputHandler(event, 'name')} type='text' placeholder='Enter your name' />
                                <input onChange={ (event) => this.inputHandler(event, 'email')} type='email' placeholder='Enter your email' />
                                <input onChange={ (event) => this.inputHandler(event, 'street')} type='text' placeholder='Enter your street' />
                                <input onChange={ (event) => this.inputHandler(event, 'zip')} type='number' placeholder='Enter your zip code' />
                                <input onChange={ (event) => this.inputHandler(event, 'country')} type='text' placeholder='Enter your country' />
                                <label>Enter Your Card Number</label>
                                <input onChange={ (event) => this.inputHandler(event, 'card')} type='text' value={this.state.cardValue} placeholder='#### #### #### ####'/>
                                <label>Enter Your Card Month</label>
                                <input onChange={ (event) => this.inputHandler(event, 'month')} type='text' value={this.state.monthValue} placeholder='##'/>
                                <label>Enter Your Card Year</label>
                                <input onChange={ (event) => this.inputHandler(event, 'year')} type='text' value={this.state.yearValue} placeholder='####'/>
                                <label>Enter Your Card CVV</label>
                                <input onChange={ (event) => this.inputHandler(event, 'cvv')} type='text' value={this.state.cvvValue} placeholder='###'/>
                                <BTN btnType='Success' btnClick={this.order}>{ this.props.loader ? 'Loading...' : 'Order'}</BTN>
                            </form>
                        </div>
                    </div>
                </div>
            </Aux>
        );
    }
}

const mapStatetoProps = state => {
    return {
        ig: state.burger.ingredients,
        price: state.burger.totalPrice,
        loader: state.order.loading,
        redirect: state.order.redirect,
        token: state.auth.token,
        uid: state.auth.userId
    }
}

const mapDispatchtoProps = dispatch => {
    return {
        buy: (orderData, token) => dispatch(actions.buyBurger(orderData, token)),
        newB: () => dispatch(actions.buyNewBurger())
    }
}

export default connect(mapStatetoProps, mapDispatchtoProps)(errorHandler(Payment, axios));