import React from 'react';
import BurgerLogo from '../../assets/logo/burger-logo.png';
import classNames from './Logo.module.css';

const Logo = (props) => {
    return (
        <div className={classNames.Logo} style={{height: props.height}} >
            <img src={BurgerLogo} alt='Logo' />
        </div>
    );
};

export default Logo;