import React from 'react';
import classNames from './BuilderController.module.css';
import BuildControl from './BuildControl/BuildControl';

const controls = [
    {label: 'Salad', type:'salad'},
    {label: 'Meat', type:'meat'},
    {label: 'Cheese', type:'cheese'},
    {label: 'Bacon', type:'bacon'},
]

const BuilderController = props => {
    return (
        <div className= {classNames.BuildControls} >
            <h1>Total Price : {props.price.toFixed(2)}$ </h1>
            {controls.map(ctrl=>{
                return <BuildControl key={ctrl.label} label={ctrl.label} addition={()=>props.iGAddition(ctrl.type)} removing={()=> props.iGRemove(ctrl.type)} disable = {props.disabledInformation[ctrl.type]} />
            })}
            <button className={classNames.OrderButton} disabled={!props.purchasable} onClick={props.modalTrigger} >Order Now</button>
        </div>
    );
};

export default BuilderController;