import React, {Component} from 'react';
import Aux from '../../hoc/Auxiliary';
import Burger from '../../components/Burger/Burger';
import Builder from '../../components/Burger/BuilderController/BuilderController';
import Modal from '../../components/UI/Modal/Modal';
import OrderSummery from '../../components/Burger/OrderSummery/OrderSummery';
import axios from '../../axios-order';
import Spinner from '../../components/UI/Spinner/Spinner';
import ErrorHandler from '../../hoc/ErrorHandler/ErrorHandler';
import {connect} from 'react-redux';
import * as burgerBuilderTypes from '../../store/actions/index'


class BurgerBuilder extends Component{
    // constructor(props){
    //     super(props)
    //     this.state = {

    //     }
    // }

    state = {
        modal: false,
        loader: true,
    }

    componentDidMount () {
        this.props.init();
        this.props.initPrice();
    }

    updatePurchasable (ingredients) {
        const sum = Object.values(ingredients).reduce((sum, el)=>{return sum + el},0);
        return sum > 0
    }

    modalTrigger = () => {
        this.setState({modal:true})
        setTimeout(() => {
            this.setState({loader:false})
        }, 1000);
    }

    modalClose = () => {
        this.setState({modal:false})
    }

    continue = () =>{
        this.props.history.push('/checkout');
    }

    render(){
        const disabledInfo = {
            ...this.props.ig
        }
        for (let key in disabledInfo){
            disabledInfo[key] = disabledInfo[key] <= 0;
        }
        let orderSummery = null;
        let burger = <Spinner />;
        if(this.props.ig && this.props.price){
            burger = (
                <Aux>
                    <Burger ingredients={this.props.ig}/>
                    <Builder iGAddition = {this.props.add} iGRemove = {this.props.remove} disabledInformation = {disabledInfo} price={this.props.price} purchasable = {this.updatePurchasable(this.props.ig)} modalTrigger={this.modalTrigger} />
                </Aux>
            )
            orderSummery = <OrderSummery ingredients={this.props.ig} canceled={this.modalClose} continue={this.continue} price={this.props.price}/>
        }
        return (
            <Aux>
                <Modal show={this.state.modal} modalClose={this.modalClose} loader={this.state.loader}>
                    {!this.state.loader ? orderSummery : <Spinner/>}
                </Modal>
                {burger}
            </Aux>
        );
    }
}

const mapStatetoProps = state => {
    return{
        ig: state.burger.ingredients,
        price: state.burger.totalPrice,
        r: state.order.redirect
    }
}
const mapDispatchtoProps = dispatch => {
    return{
        add: (igName) => dispatch(burgerBuilderTypes.addIG(igName)),
        remove: (igName) => dispatch(burgerBuilderTypes.removeIG(igName)),
        init: () => dispatch(burgerBuilderTypes.initIg()),
        initPrice: () => dispatch(burgerBuilderTypes.initPrice()),
    }
}

export default connect(mapStatetoProps, mapDispatchtoProps)(ErrorHandler(BurgerBuilder, axios));