import React from 'react';
import classNames from './CheckoutSummery.module.css';
import Burger from '../../Burger/Burger';
import BTN from '../../UI/Button/Button';

const CheckoutSummery = (props) => {
    let data = false;
    if(props.ingredients){
        data = Object.values(props.ingredients).reduce((sum, el)=>{return sum + el},0) > 0;
    }
    return (
        <div className={classNames.container}>
            {data ? <h1>We hope it taste good to you!</h1> : <h1>Please add ingredients to checkout</h1>}
            <div className={classNames.burgerCon}>
                <Burger ingredients={props.ingredients} />
            </div>
            {data ? <> <BTN btnType='Danger' btnClick = {props.cancel} > Cancel </BTN>
            <BTN btnType='Success' btnClick = {props.continue} > Continue </BTN> </> : <BTN btnType='Success' btnClick = {props.add} > Add Ingredients </BTN>}
            
        </div>
    );
};

export default CheckoutSummery;