import React from 'react';
import Aux from '../../../hoc/Auxiliary';
import BTN from '../../UI/Button/Button';

const OrderSummery = (props) => {
    const ingredientsSummery = Object.keys(props.ingredients).map(igKey=>{
        return <li key={igKey} ><span style={{textTransform:'capitalize'}} >{igKey}</span> : <span>{props.ingredients[igKey]}</span></li>
    })


    return (
        <Aux>
            <h1>Order Summery</h1>
            <h3>Total : {props.price.toFixed(2)}$ </h3>
            <p>Your burger contains following ingredients:</p>
            <ul>
                {ingredientsSummery}
            </ul>  
            <p>Continue to checkout?</p>
            <BTN btnType={'Success'} btnClick={props.continue}>Continue</BTN>
            <BTN btnType={'Danger'} btnClick={props.canceled}>Cancel</BTN> 
        </Aux>
    );
};

export default OrderSummery;