import React from 'react';
import classNames from './Toolbar.module.css';
import Logo from '../../Logo/Logo'
import Nav from '../NavItems/NavItems';

const Toolbar = (props) => {
    return (
        <div className={classNames.Toolbar}>
            <div className={classNames.t} onClick={props.menuOpen}>MENU</div>
            <Logo height='80%'/>
            <nav className={classNames.d}>
                <Nav  routeCtrlInner={props.routeCtrl} />
            </nav>
        </div>
    );
};

export default Toolbar;