import React from 'react';
import classNames from './Order.module.css';

const Order = (props) => {
    return (
        <div className={classNames.container}>
            <div className={classNames.inner}>
                <p>Ingredients: <span>Salad:{props.ig.salad}</span><span>Meat:{props.ig.meat}</span><span>Bacon:{props.ig.bacon}</span><span>Cheese:{props.ig.cheese}</span> </p>
                <h3>Total: {props.price}$</h3>
            </div>
        </div>
    );
};

export default Order;